import Vue from 'vue';
import Vuetify from 'vuetify/lib';
import 'vuetify/src/stylus/app.styl';
import '@fortawesome/fontawesome-pro/css/all.css';

Vue.use(Vuetify, {
  options: {
    minifyTheme(css) {
      return process.env.NODE_ENV === 'production'
        ? css.replace(/[\s|\r\n|\r|\n]/g, '')
        : css;
    },
    customProperties: true,
  },
  theme: {
    primary: '#004346',
    secondary: '#74B3CE',
    accent: '#BC8DA0',
    error: '#ff1744',
    info: '#172A3A',
    success: '#4CAF50',
    warning: '#ffab00',
  },
});
