

import Vue from 'vue';
import Router from 'vue-router';
// import { router } from 'sw-toolbox';
import firebase from 'firebase';

// Components for Details
// eslint-disable-next-line
const dataHistory = () => import( /* webpackChunkName: "Data History" */ './components/details/AssetData.vue');
const config = () => import(/* webpackChunkName: "Configurations" */'./components/details/AssetConfig.vue');
const diagnostics = () => import(/* webpackChunkName: "Diagnostics" */ './components/details/Diagnostics.vue');
const alertList = () => import(/* webpackChunkName: "Diagnostics" */ './components/details/Alerts.vue');

Vue.use(Router);

const router = new Router({
  base: process.env.BASE_URL,
  mode: 'hash',
  scrollBehavior(to, from, savedPosition) {
    return {
      x: 0,
      y: 0
    }
  },
  routes: [
    {
      path: '*',
      name: 'error',
      component: () => import(/* webpackChunkName: "login" */ './views/404.vue'),
    },
    {
      path: '/',
      name: 'login',
      component: () => import(/* webpackChunkName: "login" */ './views/Login.vue'),
      meta: {
        guest: true,
      },
    },
    {
      path: '/register',
      name: 'register',
      component: () => import(/* webpackChunkName: "login" */ './views/Register.vue'),
      meta: {
        guest: true,
      },
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      /* eslint-disable-next-line */
      component: () => import(/* webpackChunkName: "dashboard" */ './views/Dashboard.vue'),
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/user',
      name: 'User Settings',
      component: () => import(/* webpackChunkName: "user" */ './views/User.vue'),
      meta: {
        requiresAuth: true,
      },
      children: [
        {
          path: 'profile',
          name: 'Your Profile',
          component: () => import(/* webpackChunkName: "profile" */'./components/user/Profile.vue'),
        },
        {
          path: 'property',
          name: 'Property Settings',
          component: () => import(/* webpackChunkName: "property" */'./components/user/Property.vue'),
        },
        {
          path: 'privacy',
          name: 'Privacy Settings',
          component: () => import(/* webpackChunkName: "privacy" */'./components/user/Privacy.vue'),
        },
        {
          path: 'devices',
          name: 'Devices',
          component: () => import(/*webpackChunkName: "devices" */ './components/user/Devices.vue'),
        },
      ],
    },
    {
      path: '/details',
      name: 'details',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      /* eslint-disable-next-line */
      component: () => import(/* webpackChunkName: "details" */ './views/Details.vue'),
      meta: {
        requiresAuth: true,
      },
      children: [
        {
          path: 'data',
          name: 'Data History',
          component: dataHistory,
        },
        {
          path: 'config',
          name: 'Configurations',
          component: config,
        },
        {
          path: 'diag',
          name: 'Diagnostics',
          component: diagnostics,
        },
        {
          path: 'alerts',
          name: 'Alerts',
          component: alertList,
        },
      ],
    },
  ],
});

router.beforeEach((to, from, next) => {
  const currentUser = firebase.auth().currentUser;
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

  if (requiresAuth && !currentUser) {
    next('/');
  }
  else if (!requiresAuth && currentUser) {
    next('/dashboard');
  }
  else {
    next();
  }

});

export default router;
